import React, { useEffect, useState } from "react";
import AOS from "aos";
import "aos/dist/aos.css";
import projectsStyle from "./style/projects.module.css";
import reactIcon from "../img/react.png";
import nodeIcon from "../img/nodeJs.png";
import jsIcon from "../img/javascript.png";
import mysqlIcon from "../img/mysql.png";
import cssIcon from "../img/css.png";
import plusIcon from "../img/plus.gif";
import gitIcon from "../img/github.gif";
import linkIcon from "../img/link.gif";

import Modal from "./Modal";

const Projects = () => {
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [selectedProject, setSelectedProject] = useState(null);

  const handleOpenModal = (project) => {
    setSelectedProject(project);
    setIsModalOpen(true);
  };

  const handleCloseModal = () => {
    setSelectedProject(null);
    setIsModalOpen(false);
  };

  const openLink = (link) => {
    console.log(link, "");
    window.open(link, "_blank");
  };

  useEffect(() => {
    // AOS 
    AOS.init({
      duration: 1200, //   
      easing: "ease-in-out", //   
      once: true, //     
      mirror: true, //     
    });
  }, []);

  const mapleImg = [
    require("../mapleImg/karing.kr_.png"),
    require("../mapleImg/karing.kr_ (1).png"),
    require("../mapleImg/karing.kr_ (2).png"),
    require("../mapleImg/karing.kr_ (3).png"),
    require("../mapleImg/karing.kr_ (4).png"),
    require("../mapleImg/karing.kr_ (5).png"),
    require("../mapleImg/karing.kr_(iPhone XR).png"),
    require("../mapleImg/karing.kr_(iPhone XR) (1).png"),
    require("../mapleImg/karing.kr_(iPhone XR) (2).png"),
    require("../mapleImg/karing.kr_(iPhone XR) (3).png"),
    require("../mapleImg/karing.kr_(iPhone XR) (4).png"),
    require("../mapleImg/karing.kr_(iPhone XR) (5).png"),
  ];

  const travelImg = [
    require("../travelImg/travelMain.png"),
    require("../travelImg/travel2.png"),
    require("../travelImg/travel3.png"),
    require("../travelImg/travel4.png"),
    require("../travelImg/travel5.png"),
    require("../travelImg/travel6.png"),
    require("../travelImg/travel7.png"),
    require("../travelImg/travel8.png"),
    require("../travelImg/travel9.png"),
  ];

  const lifeupImg = [
    require("../lifeupImg/lifeup1.gif"),
    require("../lifeupImg/lifeup2.gif"),
    require("../lifeupImg/lifeup3.gif"),
    require("../lifeupImg/lifeup4.gif"),
    require("../lifeupImg/lifeup5.png"),
  ];

  const projectsFront = [
    {
      projectsName: " API   ",
      turnarouncd: "2024.01 ~ 2024.03",
      skills: [reactIcon, cssIcon, jsIcon],
      isSolo: " ",
      description: [
        " OpenAPI  ",
        "React   ",
        "      ",
        "    ",
        "      ",
        "  , karing.kr   DNS    ",
      ],
      linkIcons: [linkIcon],
      link: ["https://kyuchory.github.io/chory/"],
      images: mapleImg,
      projectOutline:
        "  Open API       .",
      roleDescription: [
        "    ,       .",
        "  Open API REST API     .",
        "await   fetch             .",
        "           .",
        "        .",
      ],
      review: [
        "       .",
        "   ,      .",
        "    ,     api  .",
        "      ,        .",
        "CSS            .",
      ],
    },
    {
      projectsName: "  ",
      turnarouncd: "2024.07 ~ 2025.01",
      isSolo: "2 ",
      description: [
        "      ",
        "Node.js, Expres.js   ",
        "Websocket      ",
        "  MySQL  , ,  ",
        "REST API    / Notion  API ",
        "Context API,     ",
      ],
      skills: [reactIcon, cssIcon, jsIcon, nodeIcon, mysqlIcon],
      myRole: ", ",
      linkIcons: [gitIcon],
      link: ["https://github.com/zzannorita/mangoAndGuava"],
      images: mapleImg,
      projectOutline:
        "             .         ,    ,   .",
      roleDescription: [
        "    ,    ",
        "      .   react-responsive   width   .",
        "axios   http       .",
        "contextAPI  websocket          ,   .",
        "MySQL  , ,      .    .",
        "Node.js, express.js     , router, controller, dao          .",
        "  , userId   11       .",
        "  type  ,  ,     .",
        " multipart/form-data    Multer      ,      .",
        "   API        .",
      ],
      review: [
        "    .     .",
        "          .",
        "node.js, express.js   ,      .",
        "           ,     .",
        " ,  , ,     dao   JS       .",
        "    ,  .",
        "    ,    ",
        "         ContextAPI  .",
        "          90%      .",
      ],
    },
    {
      projectsName: "  ",
      turnarouncd: "2023.08 ~ 2023.11",
      isSolo: "3 ",
      description: [
        "  Open API    ",
        "  REST API  ",
        " API,      ",
        "   ,    ",
      ],
      skills: [reactIcon, cssIcon, jsIcon, nodeIcon, mysqlIcon],
      myRole: ", ",
      linkIcons: [gitIcon],
      link: ["https://github.com/kim-wnsgk/travel"],
      images: travelImg,
      projectOutline: " 3  ,    ",
      roleDescription: [
        "   ,      .",
        "REST API ,            .",
        "       .",
        "    ,        .",
        "   ID       .",
        "     ,        .",
        "      ,         .",
      ],
      review: [
        " REST API    .    .",
        " , ,   HTTP           .",
        "             .",
        "  , Open API        .",
        "    ,        .",
      ],
    },
    {
      projectsName: "   ",
      turnarouncd: "2021.09 ~ 2021.11",
      isSolo: "5 ",
      description: [
        ",      ",
        "  Firebase       ",
        "ReactNative     UI ",
      ],
      skills: [reactIcon, cssIcon, jsIcon],
      myRole: ", ",
      linkIcons: [gitIcon],
      link: ["https://github.com/kyuchory/greenlight_project"],
      images: lifeupImg,
      projectOutline:
        " 5  ,    ",
      roleDescription: [
        ",        .",
        "      ,          .",
        "    iOS      React Native .",
        "    Expo  .",
        "Firebase       ,  ,  , /   .",
        ",     ,          .",
        "  CSS Flexbox   ,  Flexbox  CSS  .",
      ],
      review: [
        "     .               .",
        "      JSX     ",
        "             .",
        "            .",
        "   ,          .",
      ],
    },
  ];

  return (
    <section
      id="skills"
      className={projectsStyle.projectsContainer}
      data-aos="fade-up"
    >
      <div className={projectsStyle.projectsContentBox}>
        <div className={projectsStyle.projectsTitleBox}>
          <div>Projects</div>
        </div>
        <div className={projectsStyle.projectsDetailBox}>
          {projectsFront.map((project, index) => {
            return (
              <div className={projectsStyle.projectsProjectBox}>
                <div className={projectsStyle.projectsProjectTitleBox}>
                  <div>
                    <div>{project.projectsName}</div>
                    <div onClick={() => handleOpenModal(project)}>
                      <img src={plusIcon} alt="plusIcon" />
                    </div>
                  </div>
                  <div>
                    <div>{project.turnarouncd}</div>
                    <div>{project.isSolo}</div>
                  </div>
                </div>
                {project.myRole && (
                  <div className={projectsStyle.proejectsProjectRoleBox}>
                    <div> | {project.myRole}</div>
                  </div>
                )}
                <div className={projectsStyle.projectsProjectSkillBox}>
                  {project.skills.map((item, index) => {
                    return (
                      <div>
                        <img
                          src={item}
                          alt="skill img"
                          className={projectsStyle.skillImg}
                        />
                      </div>
                    );
                  })}
                </div>
                <div className={projectsStyle.projectsProjectContentBox}>
                  <ul>
                    {project.description.map((item, index) => {
                      return <li>{item}</li>;
                    })}
                  </ul>
                </div>
                <div className={projectsStyle.projectsProjectContactBox}>
                  {project.linkIcons &&
                    project.linkIcons.map((icon, index) => (
                      <div
                        className={projectsStyle.projectsProjectLinkIconBox}
                        onClick={() => openLink(project.link[index])}
                      >
                        <img src={icon} alt="icon" />
                      </div>
                    ))}
                </div>
              </div>
            );
          })}
        </div>
      </div>
      <Modal
        isOpen={isModalOpen}
        onClose={handleCloseModal}
        project={selectedProject}
      />
    </section>
  );
};

export default Projects;
